.navbar {
  width: 100%;
  height: auto;
  background-color: white;
  padding: 20px;
  box-shadow: 0 1px 3px rgb(0 0 0 / 10%), 0 2px 2px rgb(0 0 0 / 6%), 0 0 2px rgb(0 0 0 / 7%);
  ul {
    display: flex;
    align-items: center;
  }
  li {
    margin: 0 15px;
    font-weight: bold;
  }
  a:hover {
    color: var(--color-primary);
  }
}
.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navbar-brand img {
  width: 50px;
  height: 50px;
}
.navbar-nav-left {
  text-transform: uppercase;
  flex: 1;
  margin-left: 20px;
}
.navbar-nav-right img {
  height: 20px;
  width: 20px;
  margin-right: 10px;
}
.navbar-nav-right {
  li:first-child a {
    display: flex;
    align-items: center;
  }
  li:nth-child(2) {
    margin: 0 5px;
  }
}
.btn {
  cursor: pointer;
  display: inline-block;
  background: none;

  border: 1px #000 solid;
  border-radius: 50px;
  padding: 7px 16px;
  line-height: 1.2;
  text-align: center;
  text-decoration: none;
}
.btn-dark-outline {
  border-color: #000;
  color: #000;
}
.btn-dark-outline:hover {
  background-color: rgb(0, 0, 0, 0.06);
}
.btn-light-outline {
  border-color: #fff;
  color: #fff;
}
.btn-dark {
  background-color: #000;
  color: #fff;
}
.btn-dark:hover {
  background-color: #333;
}
